import { random, sample } from 'lodash'
import { Instance, types } from 'mobx-state-tree'

export enum WeaponVariant {
  Sword = 'SWORD'
}
const WEAPON_VARIANTS = [WeaponVariant.Sword]
const WEAPON_VARIANT_VALUES = Object.values(WeaponVariant)

export const WeaponModel = types.model({
  variant: types.enumeration<WeaponVariant>('WeaponVariant', WEAPON_VARIANT_VALUES),
  power: types.number
})

export type WeaponInstance = Instance<typeof WeaponModel>

export function genWeapon (): WeaponInstance {
  const variant = sample(WEAPON_VARIANTS) as WeaponVariant
  const power = random(1, 5)
  return {
    variant, power
  }
}
