import { random, range } from 'lodash'
import { Instance, types } from 'mobx-state-tree'
import { LandInstance } from './land'

import Node, { newNode } from './node'

const Level = types
  .model({
    id: types.identifierNumber,
    nodes: types.late(() => types.array(types.reference(Node)))
  })
  .actions(self => ({
    spawnNodes (_land: any) {
      const land = _land as LandInstance

      const nodesN = random(3, 5)
      const [idStart, idEnd] = land.spawnNodeIds(nodesN)
      const ids = range(idStart, idEnd + 1)

      let index = 0
      ids.forEach(id => {
        const node = newNode({
          id,
          level: self.id,
          indexInBatch: index,
          batchSize: nodesN
        })
        land.nodes.put(node)
        self.nodes.push(node)

        index += 1
      })
    }
  }))

export default Level
export type LevelInstance = Instance<typeof Level>
