import React, { useCallback, useState } from 'react'
import { observer } from 'mobx-react-lite'
import TWEEN, { Easing, Tween } from '@tweenjs/tween.js'
import { IonContent, IonPage } from '@ionic/react'
import ReactFlow, { Background, BackgroundVariant } from 'react-flow-renderer'

import { FlowEdge } from '../models/edge'
import { useLand } from '../models/land'
import { FlowNode, NodeInstance } from '../models/node'

import Node from '../components/Node'
import Label from '../components/Label'

import './Game.css'
import useWindowSize from '../utils/useWindowSize'

const TRANSITION_DURATION = 300
const TRANSITION_EASING = Easing.Quadratic.Out

// Setup the animation loop.
function animate (time: number): void {
  requestAnimationFrame(animate)
  TWEEN.update(time)
}
requestAnimationFrame(animate)

const Game: React.FC = observer(() => {
  const windowSize = useWindowSize()
  const focusPosX = windowSize.innerWidth / 2
  const focusPosY = windowSize.innerHeight / 3 * 2

  const land = useLand()
  const landData = land.flowData

  const [flowInstance, setFlowInstance] = useState<any>(null) // TODO

  const onLoad = useCallback((instance) => {
    setFlowInstance(instance)
    instance.fitView()
    panToRogue(instance)
  }, [])

  const panToNode = (instance: any, node: NodeInstance): void => {
    const {
      position: [x, y],
      zoom
    } = instance.toObject()

    const nodeX = 0 - node.posX + focusPosX
    const nodeY = 0 - node.posY + focusPosY

    new Tween({ x, y, zoom })
      .to(
        { x: nodeX, y: nodeY, zoom: 1 },
        TRANSITION_DURATION
      )
      .easing(TRANSITION_EASING)
      .onUpdate(({ x, y, zoom }) => {
        instance.setTransform({ x, y, zoom })
      })
      .start()
  }

  const panToRogue = (instance: any): void => {
    panToNode(instance, land.rogueNode as NodeInstance)
  }

  const onElementClick = (_event: React.MouseEvent, element: FlowNode | FlowEdge): void => {
    function elementIsNode (element: any): element is FlowNode {
      return element.position
    }

    if (!elementIsNode(element)) {
      return
    }

    const target = land.nodes.get(element.id) as NodeInstance
    if (!land.isReachable(target)) {
      return
    }

    land.move(target)

    panToRogue(flowInstance)
  }

  return (
    <IonPage>
      <IonContent fullscreen>

        <ReactFlow
          nodeTypes={{ node: Node }}
          elements={landData}
          elementsSelectable={false}
          nodesDraggable={false}
          nodesConnectable={false}
          onLoad={onLoad}
          onElementClick={onElementClick}
        >
          <Background variant={BackgroundVariant.Lines} />
        </ReactFlow>

        <Label />

      </IonContent>
    </IonPage>
  )
})

export default Game
