import { remove, sample } from 'lodash'
import { useContext, createContext } from 'react'
import { Instance, onSnapshot, types } from 'mobx-state-tree'

import Node, { NodeInstance } from './node'
import Edge, { EdgeInstance } from './edge'
import Level, { LevelInstance } from './level'
import Rogue from './rogue'
import { NodeType } from './node-data'
import { genEdgeData } from './edge-data'

export const Land = types
  .model({
    id: types.identifier,
    nodes: types.map(Node),
    maxNodeId: -1,
    levels: types.map(Level),
    maxLevelId: -1,
    edges: types.map(Edge), // TODO why not array of edges. there was a reason and i forgot :|
    // NOTE only has to be a safeRef instead of a ref
    // cuz a issue around resetting rogueNode
    rogueNode: types.safeReference(Node),
    rogue: Rogue
  })
  .views(self => ({
    edgeBetweenNodes (source: NodeInstance, target: NodeInstance): EdgeInstance | undefined {
      for (const edge of Array.from(self.edges.values())) {
        if (edge.source === source && edge.target === target) {
          return edge
        }
      }
    }
  }))
  .views(self => ({
    isReachable (node: NodeInstance): boolean {
      // TODO loop in rogueNode's outgoing edges
      const rogueLevel = self.rogueNode?.level as LevelInstance
      return rogueLevel.id + 1 === node.level.id
    }
  }))
  .views(self => ({
    get flowData () { // TODO -> flowData
      const nodesData = Array.from(self.nodes.values()).map(node => node.flowData)
      const edgesData = Array.from(self.edges.values()).map(edge => edge.flowData(self.isReachable(edge.target)))
      return [...nodesData, ...edgesData]
    }
  }))
  .actions(self => ({
    spawnNodeId (): number {
      self.maxNodeId += 1
      return self.maxNodeId
    },
    spawnNodeIds (n: number): [start: number, end: number] {
      const start = self.maxNodeId + 1
      const end = start + n - 1
      self.maxNodeId = end
      return [start, end]
    },
    spawnEdge (source: number, target: number) {
      const id = `e-${source}-${target}`
      const edge = Edge.create({
        id,
        source: source,
        target: target,
        data: genEdgeData()
      })
      self.edges.put(edge)
      edge.target.incomingEdgesN += 1
      edge.source.outgoingEdgesN += 1
    },
    // recursively destroy node, outgoing edges, and newly become unreachable nodes
    destroyNode (node: NodeInstance) {
      if (node === self.rogueNode) { return }

      // destroy out edges
      // TODO ref edges in node? too complex for sure
      self.edges.forEach((edge, edgeId) => {
        if (edge.source.id === node.id) {
          const target = edge.target

          edge.target.incomingEdgesN -= 1
          edge.source.outgoingEdgesN -= 1
          self.edges.delete(edgeId)

          // recursive
          if (target.incomingEdgesN === 0) {
            // @ts-ignore
            self.destroyNode(target)
          }
        }
      })

      remove(node.level.nodes, (nodeInLevel) => nodeInLevel === node)
      self.nodes.delete(node.id.toString())
    }
  }))
  .actions(self => ({
    spawnEdgesBetween (prevLevel: LevelInstance, nextLevel: LevelInstance) {
      let i = 0
      let j = 0

      const spawn = (): void => {
        self.spawnEdge(prevLevel.nodes[i].id, nextLevel.nodes[j].id)
      }

      spawn()
      while (true) {
        const cases = []

        if (i < prevLevel.nodes.length - 1) {
          cases.push('PREV_LEVEL_STEP')
        }

        if (j < nextLevel.nodes.length - 1) {
          cases.push('NEXT_LEVEL_STEP')
        }

        if (cases.length === 0) {
          break
        }

        switch (sample(cases)) {
          case 'PREV_LEVEL_STEP':
            i += 1
            break
          case 'NEXT_LEVEL_STEP':
            j += 1
            break
        }
        spawn()
      }
    }
  }))
  .actions(self => ({
    spawnLevel () {
      const prevLevel = self.levels.get(self.maxLevelId.toString()) as LevelInstance
      self.maxLevelId += 1
      const newLevelId = self.maxLevelId
      const level = Level.create({
        id: newLevelId,
        nodes: []
      })

      // spawn nodes
      level.spawnNodes(self)
      self.levels.put(level)

      // spawn edges
      self.spawnEdgesBetween(prevLevel, level)
    }
  }))
  .actions(self => ({
    init () {
      const node = Node.create({
        id: 0,
        level: 0,
        posX: 0,
        posY: 0,
        data: {
          type: NodeType.Rogue
        }
      })
      self.nodes.put(node)
      self.levels.put(Level.create({
        id: 0,
        nodes: [0]
      }))

      self.rogueNode = node

      self.maxLevelId = 0
      self.maxNodeId = 0

      self.spawnLevel()
      self.spawnLevel()
      self.spawnLevel()
    }
  }))
  .actions(self => ({
    reset () {
      self.levels.clear()
      self.nodes.clear()
      self.edges.clear()
      self.maxLevelId = -1
      self.maxNodeId = -1

      self.rogue.reset()

      self.init()
    }
  }))
  .actions(self => ({
    move (target: NodeInstance) {
      const prevLevel = self.rogueNode?.level as LevelInstance
      // const targetLevel = target.level as LevelInstance

      const edge = self.edgeBetweenNodes(self.rogueNode as NodeInstance, target) as EdgeInstance

      // interact with edge
      self.rogue.interactWithEdge(edge)
      if (self.rogue.isDead) {
        self.reset()
        return
      }

      // interact with nodee
      self.rogue.interactWithNode(target)
      if (self.rogue.isDead) {
        self.reset()
        return
      }

      // move
      target.data = { type: NodeType.Rogue }
      self.rogueNode = target
      self.rogue.distance += 1

      // destroy previous level (and thus unavailable nodes)
      prevLevel.nodes.forEach((node) => {
        self.destroyNode(node)
      })
      self.levels.delete(prevLevel.id.toString())

      // spawn a new level
      self.spawnLevel()
    }
  }))

export const landStore = Land.create({
  id: 'land',
  rogueNode: 0,
  rogue: {}
})
landStore.init()

onSnapshot(landStore, snapshot => {
  console.log('landStore.onSnapshot: ', snapshot)
  // localStorage.setItem('rootState', JSON.stringify(snapshot))
})

export type LandInstance = Instance<typeof Land>
export const LandStoreContext = createContext<null | LandInstance>(null)

export function useLand (): LandInstance {
  const store = useContext(LandStoreContext)
  if (store === null) {
    throw new Error('Store cannot be null, please add a context provider')
  }
  return store
}
