import { useLand } from '../models/land'
import { FlowNode } from '../models/node'
import { NodeType } from '../models/node-data'
import { PotionVariant } from '../models/potion'
import CommonNode from './nodes/CommonNode'
import RogueNode from './nodes/RogueNode'

const Node = (props: FlowNode): JSX.Element | null => {
  const land = useLand()
  const node = land.nodes.get(props.id)

  // TODO HACK
  // dont know why ReactFlow renders Node for deleted node
  if (node === undefined) {
    return null
  }

  switch (node.data.type) {
    case NodeType.Rogue:
      return <RogueNode />
    case NodeType.Blank:
      return <CommonNode icon='‚õ∫Ô∏è' />
    case NodeType.Monster:
      return <CommonNode icon='üëæ' number={node.data.power} />
    case NodeType.Weapon:
      return <CommonNode icon='‚öîÔ∏è' number={node.data.weapon.power} />
    case NodeType.Potion:
      switch (node.data.potion.variant) {
        case PotionVariant.Hearts:
          return <CommonNode icon='üíï' number={node.data.potion.power} />
        case PotionVariant.Healing:
          return <CommonNode icon='üß™' number={node.data.potion.power} />
      }
      break
    default:
      console.warn('DontKnowHowToRenderNode', JSON.stringify(node.data))
      return <></>
  }
}

export default Node
