import { random } from 'lodash'
import { IAnyModelType, Instance, types } from 'mobx-state-tree'

import * as reactFlow from 'react-flow-renderer'

import Level from './level'
import { genNodeData, NodeDataType } from './node-data'

interface NodeData { label: string }

export type FlowNode = reactFlow.Node<NodeData>

export const NODE_H = 36

const Node = types
  .model({
    id: types.identifierNumber,
    level: types.reference(types.late((): IAnyModelType => Level)),
    incomingEdgesN: 0,
    outgoingEdgesN: 0,
    posX: types.number,
    posY: types.number,

    data: NodeDataType
  })
  .views(self => ({
    get flowClass (): string {
      return `node ${self.data.type}`
    }
  }))
  .views(self => ({
    get flowData (): FlowNode {
      return {
        id: `${self.id}`,
        position: {
          x: self.posX,
          y: self.posY
        },
        type: 'node', // orverride
        className: self.flowClass
      }
    }
  }))

export type NodeInstance = Instance<typeof Node>

export function newNode ({ id, level, indexInBatch, batchSize }: {
  id: number
  level: number
  indexInBatch: number
  batchSize: number
}): NodeInstance {
  const MARGIN_BETWEEN_NODES = 150
  const deltaX = 0 - (batchSize - 1) / 2 * MARGIN_BETWEEN_NODES
  let posX = indexInBatch * MARGIN_BETWEEN_NODES + deltaX
  let posY = level * -150

  // TODO
  posX += random(-20, 20)
  posY += random(-20, 20)

  const data = genNodeData()

  return Node.create({
    id,
    level,
    posX,
    posY,
    data
  })
}

export default Node
