import { random, sample } from 'lodash'
import { Instance, types } from 'mobx-state-tree'
import { genPotion, PotionModel } from './potion'
import { genWeapon, WeaponModel } from './weapon'

export enum NodeType {
  Rogue = 'ROGUE',
  Blank = 'BLANK',
  Monster = 'MONSTER',
  Weapon = 'WEAPON',
  Potion = 'POTION'
}

type GenableNodeType = NodeType.Blank | NodeType.Monster | NodeType.Weapon | NodeType.Potion
const GENABLE_NODE_TYPES = [
  NodeType.Blank, NodeType.Monster, NodeType.Weapon, NodeType.Potion
]

function genNodeType (): GenableNodeType {
  return sample(GENABLE_NODE_TYPES) as GenableNodeType
}

export const NodeDataType = types.union(
  types.model({ type: types.literal(NodeType.Rogue) }),
  types.model({ type: types.literal(NodeType.Blank) }),
  types.model({ type: types.literal(NodeType.Monster), power: types.number }), // TODO monster model
  types.model({ type: types.literal(NodeType.Weapon), weapon: WeaponModel }),
  types.model({ type: types.literal(NodeType.Potion), potion: PotionModel })
)

export function genNodeData (): Instance<typeof NodeDataType> {
  const type = genNodeType()
  switch (type) {
    case NodeType.Blank:
      return { type }
    case NodeType.Monster: {
      const power = random(1, 5)
      return { type, power }
    }
    case NodeType.Weapon: {
      return {
        type,
        weapon: genWeapon()
      }
    }
    case NodeType.Potion: {
      return {
        type,
        potion: genPotion()
      }
    }
  }
}
