import { random, sample } from 'lodash'
import { Instance, types } from 'mobx-state-tree'

export enum EdgeType {
  Blank = 'BLANK',
  Trap = 'TRAP',
}

export const EdgeDataType = types.union(
  // blank
  types.model({ type: types.literal(EdgeType.Blank) }),
  // trap
  types.model({
    type: types.literal(EdgeType.Trap),
    power: types.number
  })
)

export function genEdgeData (): Instance<typeof EdgeDataType> {
  const type = sample([
    EdgeType.Blank,
    EdgeType.Trap
  ]) as EdgeType

  switch (type) {
    case EdgeType.Blank:
      return { type }
    case EdgeType.Trap: {
      const power = random(1, 3)
      return {
        type,
        power
      }
    }
  }
}
