import { sample } from 'lodash'
import { Instance, types } from 'mobx-state-tree'
import * as reactFlow from 'react-flow-renderer'
import { EdgeDataType, EdgeType } from './edge-data'

import Node from './node'

export type FlowEdge = reactFlow.Edge<any>

const Edge = types
  .model({
    id: types.identifier,
    source: types.reference(Node),
    target: types.reference(Node),
    data: EdgeDataType
  })
  .views(self => ({
    get label (): string | null {
      switch (self.data.type) {
        case EdgeType.Blank:
          return null
        case EdgeType.Trap: {
          // TODO
          const icon = sample(['üåä', 'ü™§', 'üî•', 'üå™']) as string
          return `${icon}${self.data?.power}`
        }
        default:
          console.warn('CantFigureEdgeLabel', self.data)
          return null
      }
    }
  }))
  .views(self => ({
    flowData (animated: boolean = false): FlowEdge {
      return {
        id: self.id,
        source: self.source.id.toString(),
        target: self.target.id.toString(),
        label: self.label,
        type: 'default',
        animated
      }
    }
  }))

export default Edge
export type EdgeInstance = Instance<typeof Edge>
