import { random, sample } from 'lodash'
import { Instance, types } from 'mobx-state-tree'

export enum PotionVariant {
  Hearts = 'HEARTS',
  Healing = 'HEALING'
}
const POTION_VARIANTS = [PotionVariant.Hearts, PotionVariant.Healing]
const POTION_VARIANT_VALUES = Object.values(PotionVariant)

export const PotionModel = types.model({
  variant: types.enumeration<PotionVariant>('PotionVariant', POTION_VARIANT_VALUES),
  power: types.number
})

export type PotionInstance = Instance<typeof PotionModel>

export function genPotion (): PotionInstance {
  const variant = sample(POTION_VARIANTS) as PotionVariant
  const power = random(1, 5)
  return {
    variant, power
  }
}
