import { Instance, types } from 'mobx-state-tree'
import { EdgeInstance } from './edge'
import { EdgeType } from './edge-data'
import { NodeInstance } from './node'
import { NodeType } from './node-data'
import { PotionInstance, PotionVariant } from './potion'
import { WeaponModel } from './weapon'

const INIT_HP = 10

const Rogue = types
  .model({
    distance: 0,
    hp: INIT_HP,
    maxHp: INIT_HP,
    weapon: types.maybe(WeaponModel),
    coins: 0
  })
  .views(self => ({
    get isDead () {
      return self.hp <= 0
    }
  }))
  .actions(self => ({
    damage (damage: number) {
      self.hp -= damage
    }
  }))
  .actions(self => ({
    fight (enemyPower: number) {
      // TODO shield

      if (self.weapon !== undefined) {
        const damage = Math.min(self.weapon.power, enemyPower)
        self.weapon.power -= damage
        enemyPower -= damage

        if (self.weapon.power === 0) {
          self.weapon = undefined
        }

        if (enemyPower === 0) {
          return
        }
      }

      self.damage(enemyPower)
    },
    drink (potion: PotionInstance) {
      switch (potion.variant) {
        case PotionVariant.Hearts:
          self.maxHp += potion.power
          self.hp += potion.power
          return
        case PotionVariant.Healing:
          self.hp = Math.min(self.hp + potion.power, self.maxHp)
          return
      }
    }
  }))
  .actions(self => ({
    reset () {
      self.distance = 0
      self.hp = INIT_HP
      self.maxHp = INIT_HP
      self.coins = 0
      self.weapon = undefined
    },
    interactWithEdge (edge: EdgeInstance) {
      switch (edge.data.type) {
        case EdgeType.Blank:
          break
        case EdgeType.Trap:
          self.damage(edge.data.power)
          break
      }
    },
    interactWithNode (node: NodeInstance) {
      switch (node.data.type) {
        case NodeType.Blank:
          break
        case NodeType.Monster:
          self.fight(node.data.power)
          break
        case NodeType.Weapon:
          self.weapon = { ...node.data.weapon }
          break
        case NodeType.Potion:
          self.drink(node.data.potion)
          break
        default:
          console.warn('DontKnowHowToInteractWithNode')
      }
    }
  }))

export type RogueInstance = Instance<typeof Rogue>

export default Rogue
