import { observer } from 'mobx-react-lite'
import { useLand } from '../models/land'

const Coins = observer(() => {
  const land = useLand()
  const rogue = land.rogue

  return (
    <div id='coins-label'>
      coins {rogue.coins}
      <br />
      distance {rogue.distance}
    </div>
  )
})

export default Coins
